/*Modal Styling */
.modal-body {
  color: var(--dark);
}

.img {
  text-align: center;
  display: block;
  justify-content: center;
  align-items: center;
  margin: auto;
  color: var(--light);
}

/* Style the buttons */
.btn {
  font-size: 1.1rem;
  text-decoration: none;
  padding: 10px 5px;
  width: 100%;
  display: block;
  margin: 15px 0;
  border-radius: var(--border-radius);
  color: var(--light);
  border: none;
  outline: 2px dashed var(--pink);
  background-color: var(--dark);
  transform: translate(-1px, -1px);
}

.btn:hover {
  color: var(--pink);
  background-color: var(--dark);
  transform: translate(0, 0);
  /* box-shadow: 2px 2px var(--green); */
}

.loader {
  border: 16px solid var(--light); /* Light grey */
  border-top: 16px solid var(--pink); /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
