/* Cards Styling  */
.card {
  /* border: 2px solid var(--dark); */
  /* border-radius: var(--border-radius); */
  color: var(--light);
  background-color: var(--dark);
  margin: 0.625rem 0;
  transition: all 0.5s ease-in-out;
}

.img {
  text-align: center;
  display: block;
  justify-content: center;
  align-items: center;
  margin: auto;
  color: var(--light)
}

.card-header {
  color: var(--light);
  background-color: var(--dark);
  padding: 0.25rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card-body {
  padding: 0.25rem;
}

.card-item {
  margin: 0.25rem 0;
  padding: 0 0.25rem;
  background-color: var(--light);
  list-style: none;
  border-radius: var(--border-radius);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

/* Style the buttons */
.btn {
  font-size: 1.1rem;
  text-decoration: none;
  padding: 10px 5px;
  width: 100%;
  display: block;
  margin: 15px 0;
  border-radius: var(--border-radius);
  color: var(--white);
  border: none;
  outline: 2px dashed var(--pink);
  background-color: var(--dark);
  transform: translate(-1px, -1px);
}

.btn:hover {
  color: var(--pink);
  background-color: var(--dark);
  transform: translate(0, 0);
  /* box-shadow: 2px 2px var(--green); */
}
